import * as BABYLON from "@babylonjs/core";

import * as LiLGUI from "lil-gui";

import { FluidSimulationDemoBase } from "./fluidSimulationDemoBase";
import { ICollisionShape } from "./Utils/sdfHelper";
import myBox from "../assets/scenes/scene_b.glb";
export class FluidSimulationDemoBoxSphere extends FluidSimulationDemoBase {
    private _checkXZBounds: boolean;
    private _sphereMesh: BABYLON.Nullable<BABYLON.Mesh> = null;
    private _boxMin: BABYLON.Vector3;
    private _boxMax: BABYLON.Vector3;
    private _boxMesh: BABYLON.Nullable<BABYLON.Mesh>;
    private _boxMaterial: BABYLON.Nullable<BABYLON.PBRMaterial>;
    private _boxMeshFront: BABYLON.Nullable<BABYLON.Mesh>;
    private _boxMaterialFront: BABYLON.Nullable<BABYLON.PBRMaterial>;
    private _origCollisionPlanes: Array<BABYLON.Plane>;
    private _collisionPlanes: Array<
        [BABYLON.Nullable<BABYLON.Mesh>, ICollisionShape]
    >;
    private _angleX: number;
    private _angleY: number;
    private _prevTransfo: BABYLON.Matrix;
    private _autoRotateBox: boolean;
    private _wallMesh: BABYLON.Nullable<BABYLON.Mesh> = null;
    // private _passPP: BABYLON.PostProcess;
    private _sceneRenderObserver: BABYLON.Nullable<
        BABYLON.Observer<BABYLON.Scene>
    >;
    private _sceneAfterCameraRenderObserver: BABYLON.Nullable<
        BABYLON.Observer<BABYLON.Camera>
    >;
    private _sceneKeyboardObserver: BABYLON.Nullable<
        BABYLON.Observer<BABYLON.KeyboardInfo>
    >;
    private _onEngineResizeObserver: BABYLON.Nullable<
        BABYLON.Observer<BABYLON.Engine>
    >;
    private _customRootMesh :BABYLON.Nullable<BABYLON.Mesh> = null;
    private _globalScale = 0.85;

    constructor(scene: BABYLON.Scene) {
        super(scene);

        const halfSize = 0.35 * this._globalScale;
        this._boxMin = new BABYLON.Vector3(-halfSize, -halfSize, -halfSize);
        this._boxMax = new BABYLON.Vector3(halfSize, halfSize, halfSize);
        this._boxMesh = null;
        this._boxMaterial = null;
        this._boxMeshFront = null;
        this._boxMaterialFront = null;

        this._checkXZBounds = true;
        this._origCollisionPlanes = [
            new BABYLON.Plane(0, 0, -1, Math.abs(this._boxMax.z)),
            new BABYLON.Plane(0, 0, 1, Math.abs(this._boxMin.z)),
            new BABYLON.Plane(1, 0, 0, Math.abs(this._boxMin.x)),
            new BABYLON.Plane(-1, 0, 0, Math.abs(this._boxMax.x)),
            new BABYLON.Plane(0, -1, 0, Math.abs(this._boxMax.y)),
            new BABYLON.Plane(0, 1, 0, Math.abs(this._boxMin.y)),
            new BABYLON.Plane(0, 1, 0, Math.abs(this._boxMin.y)),
        ];
        this._collisionPlanes = [];
        for (let i = 0; i < this._origCollisionPlanes.length; ++i) {
            const plane = this._origCollisionPlanes[i];
            this.addCollisionPlane(
                plane.normal,
                plane.d,
                i === this._origCollisionPlanes.length - 1 ? 0.98 : undefined
            );
        }

        this._angleX = 0;
        this._angleY = 0;
        this._prevTransfo = BABYLON.Matrix.Identity();
        this._autoRotateBox = false;

        this._sceneRenderObserver = null;
        this._sceneAfterCameraRenderObserver = null;
        this._sceneKeyboardObserver = null;
        this._onEngineResizeObserver = null;

        // this._passPP = new BABYLON.PassPostProcess(
        //     "pass",
        //     1,
        //     null,
        //     undefined,
        //     this._engine
        // );
        // this._passPP.externalTextureSamplerBinding = true;

        this.loadCustomModel();
        this._setCustomDebugLayer();

    }

    _setCustomDebugLayer() {
        type di = {
            [key: string]: boolean
          }
          const debugKeyInfo: di = {
            'Shift': false,
            'D': false
          }
          this._scene.onKeyboardObservable.add((kbInfo) => {
            switch (kbInfo.type) {
              case BABYLON.KeyboardEventTypes.KEYDOWN:
                debugKeyInfo[kbInfo.event.key] = true
                break;
              case BABYLON.KeyboardEventTypes.KEYUP:
                debugKeyInfo[kbInfo.event.key] = false
                break;
            }
            if (debugKeyInfo['Shift'] && debugKeyInfo['D']) {
              this._scene.debugLayer.isVisible() ? this._scene.debugLayer.hide() : this._scene.debugLayer.show({
                overlay: true
              })
            }
          });
    }

    loadCustomModel() {
        let boolBox: BABYLON.Mesh | undefined = undefined;
        let water: BABYLON.Mesh | undefined = undefined;
        let waterWave: BABYLON.Mesh | undefined = undefined;
        BABYLON.SceneLoader.AppendAsync("", myBox, this._scene).then((importResult) => {
            // const eu = new BABYLON.Vector3(0, 0,20 / 180 * Math.PI)
            // eu.reorderInPlace("YXZ")
            // importResult.meshes[0].rotateAround(BABYLON.Vector3.Zero(), BABYLON.Vector3.Right(), 0 / 180 * Math.PI)
            this._customRootMesh = importResult.getMeshByName("__root__") as BABYLON.Mesh;
            const scaleMesh = 0.1 * this._globalScale;
            this._customRootMesh?.scaling.set(scaleMesh,scaleMesh,scaleMesh)
      
            const lightConfig = [
              {
                intensity: 0,
                diffuseColor: BABYLON.Color3.FromInts(0.686 * 255, 255, 0.275 * 255),
                specularColor: BABYLON.Color3.FromInts(0, 0.871 * 255, 255),
                position: new BABYLON.Vector3(0, 0, -6.294),
              },
              {
                intensity: 0,
                diffuseColor: BABYLON.Color3.FromInts(0, 0.863 * 255, 255),
                specularColor: BABYLON.Color3.FromInts(0, 255, 0.678 * 255),
                position: new BABYLON.Vector3(0.178, -0.037, 6.285),
              }
            ]
            let lightNode: BABYLON.TransformNode|undefined = undefined;
            importResult.lights.forEach((light, index) => {
              const pLight = light as BABYLON.PointLight;
              if (lightNode === undefined) {
                lightNode = pLight.parent as BABYLON.TransformNode;
                lightNode.position.set(0, 0, 0);
              }
              lightNode.position.set(0,0,0);
              pLight.parent = lightNode;
              pLight.position.copyFrom(lightConfig[index].position);
              pLight.intensity = lightConfig[index].intensity;
              pLight.diffuse.copyFrom(lightConfig[index].diffuseColor);
              pLight.specular.copyFrom(lightConfig[index].specularColor);
            })
      
            this._scene.registerBeforeRender(() => {
              if (lightNode !== undefined) {
                lightNode.rotationQuaternion = null
                lightNode.rotation.y = ((this._scene.activeCameras?.[0] ?? this._scene.activeCamera) as any)!.alpha + Math.PI;
              }
            });
      
            // const probe = new ReflectionProbe("boxReflection", 512, scene);
      
            const renderList: any[] = []
            importResult.meshes.forEach((mesh)=> {
              if (mesh && mesh.name && mesh.material) {
            //   (mesh.material as BABYLON.PBRMaterial).maxSimultaneousLights = 8
              switch (mesh.name) {
                case "立方体.006_Baked":
                  mesh.material.alpha = 0.4
                  mesh.alphaIndex = 4;
                  mesh.material.transparencyMode = BABYLON.Material.MATERIAL_ALPHABLEND
                  mesh.material.alphaMode = BABYLON.Engine.ALPHA_COMBINE;
                  // eslint-disable-next-line no-case-declarations
                  const boxMat: BABYLON.PBRMaterial = (mesh.material as BABYLON.PBRMaterial);
                  boxMat.metallic = 1;
                  boxMat.roughness = 0.2;
                  boxMat.albedoColor.r = 0.50;
                  boxMat.albedoColor.g = 0.04;
                  boxMat.albedoColor.b = 1.0;
                  boxMat.metallicReflectanceColor.r = 0.97;
                  boxMat.metallicReflectanceColor.g = 0.029;
                  boxMat.metallicReflectanceColor.b = 1.0;
                  boxMat.anisotropy.isEnabled = true;
                  boxMat.anisotropy.intensity = 0.8;
                  boxMat.sheen.isEnabled = true;
                  boxMat.sheen.intensity = 1.0;
                  boxMat.sheen.color.r = 0.816;
                  boxMat.sheen.color.g = 0.006;
                  boxMat.sheen.color.b = 1.0;
                  boxMat.sheen.useRoughnessFromMainTexture = true;
                  boxMat.subSurface.tintColor.r = 0.541;
                  boxMat.subSurface.tintColor.g = 0.933;
                  boxMat.subSurface.tintColor.b = 1.0;
                  boxMat.subSurface.isScatteringEnabled = true;
                  boxMat.subSurface.isTranslucencyEnabled = true;
                  boxMat.disableDepthWrite = true
      
                  break;
                case "布尔":
                  mesh.visibility = 0
                  boolBox = mesh as BABYLON.Mesh;
                  break;
                case "柱体.001_Baked":
                  mesh.visibility = 0
                  break;
                case "平面_primitive0":
                  mesh.visibility = 0
                  water = mesh as BABYLON.Mesh;
                  break;
                case "平面_primitive1":
                  mesh.visibility = 0
                  waterWave = mesh as BABYLON.Mesh;
                  break;
                case "边线_Baked":
                  mesh.visibility = 0
                  break;
                case "立方体.011_Baked":
                  renderList.push(mesh)
                  mesh.alphaIndex = 3
                  // mesh.material.disableDepthWrite = true;
                  mesh.material.alpha = 0.6
                  mesh.material.transparencyMode = BABYLON.Material.MATERIAL_ALPHABLEND;
                  mesh.material.alphaMode = BABYLON.Engine.ALPHA_COMBINE;
                  // (mesh.material as PBRMaterial).emissiveColor = Color3.FromInts(0.141,0, 0.482);
                  (mesh.material as BABYLON.PBRMaterial).emissiveColor = BABYLON.Color3.FromInts(3.4,0, 51);
                  (mesh.material as BABYLON.PBRMaterial).metallic = 1.0;
                  (mesh.material as BABYLON.PBRMaterial).roughness = 0.0;
                  break;
                case "立方体.013_Baked":
                  renderList.push(mesh)
                  mesh.alphaIndex = 3
                  // mesh.material.disableDepthWrite = true;
                  mesh.material.alpha = 0.4
                  mesh.material.transparencyMode = BABYLON.Material.MATERIAL_ALPHABLEND;
                  mesh.material.alphaMode = BABYLON.Engine.ALPHA_COMBINE;
                  (mesh.material as BABYLON.PBRMaterial).emissiveColor = BABYLON.Color3.FromInts(12.6,0, 148);
                  (mesh.material as BABYLON.PBRMaterial).metallic = 0.93;
                  (mesh.material as BABYLON.PBRMaterial).roughness = 0.0;
                  break;
                case "立方体.012_Baked":
                case "立方体.014_Baked":
                  renderList.push(mesh)
                  mesh.alphaIndex = 3
                  mesh.material.alpha = 0.26
                  mesh.material.transparencyMode = BABYLON.Material.MATERIAL_ALPHABLEND
                  mesh.material.alphaMode = BABYLON.Engine.ALPHA_ADD;
                  (mesh.material as BABYLON.PBRMaterial).emissiveColor = BABYLON.Color3.FromInts(0.059,0.161, 1.0);
                  (mesh.material as BABYLON.PBRMaterial).metallic = 1.0;
                  (mesh.material as BABYLON.PBRMaterial).roughness = 0.15;
                  break;
                case "Cylinder":
                  break;
                default:
                  break;
              }
              }
            })
          })
    }

    protected async _run() {
        // Get collision meshes
        for (let i = 0; i < this._origCollisionPlanes.length; ++i) {
            this._collisionPlanes.push(this._collisionObjects[i]);
        }

        this._collisionPlanes[this._collisionPlanes.length - 1][1].disabled = true;

        // Reset camera
        const camera = this._scene.activeCameras?.[0] ?? this._scene.activeCamera;
        
        const tempScene = new BABYLON.Scene(this._scene.getEngine());
        const tempCamera = new BABYLON.ArcRotateCamera("camera", 14.33, 1.594, this._globalScale, BABYLON.Vector3.Zero(), tempScene);
        tempCamera.lowerRadiusLimit = this._globalScale;
        tempCamera.upperRadiusLimit = this._globalScale;
        tempCamera.useAutoRotationBehavior = true;
        tempCamera.attachControl(document.getElementById("renderCanvas"), true);
        tempScene.attachControl(true,true,true);
        tempScene.clearColor = new BABYLON.Color4(0, 0, 0, 0);
        tempScene.autoClear = false

        this._scene.onAfterCameraRenderObservable.add(() => {
            tempScene.render()
            if (this._customRootMesh) {
                this._customRootMesh.rotationQuaternion = tempCamera.absoluteRotation;
                this._rotateMeshes(-tempCamera.alpha, -tempCamera.beta);
            }
        })

        if (camera) {
            (camera as BABYLON.ArcRotateCamera).alpha = 1;
            (camera as BABYLON.ArcRotateCamera).beta = 1.1;
            (camera as BABYLON.ArcRotateCamera).radius = 1.9;
            (camera as BABYLON.ArcRotateCamera).panningSensibility = 0;
            (camera as BABYLON.ArcRotateCamera).lowerAlphaLimit = 1;
            (camera as BABYLON.ArcRotateCamera).upperAlphaLimit = 1;
            (camera as BABYLON.ArcRotateCamera).lowerBetaLimit = 1.1;
            (camera as BABYLON.ArcRotateCamera).upperBetaLimit = 1.1;
            (camera as BABYLON.ArcRotateCamera).lowerRadiusLimit = 1.9 * this._globalScale;
            (camera as BABYLON.ArcRotateCamera).upperRadiusLimit = 1.9 * this._globalScale;

            // camera.outputRenderTarget = new BABYLON.RenderTargetTexture(
            //     "rttFinal",
            //     {
            //         width: this._engine.getRenderWidth(),
            //         height: this._engine.getRenderHeight(),
            //     },
            //     this._scene
            // );
        }

        // Simulation parameters
        this._fluidRenderObject.object.particleSize = 0.03;
        this._fluidRenderObject.object.particleThicknessAlpha = 0.017;

        this._fluidSim!.smoothingRadius = 0.05;
        this._fluidSim!.densityReference = 37100;
        this._fluidSim!.pressureConstant = 3;
        this._fluidSim!.viscosity = 0.01;
        this._fluidSim!.maxVelocity = 6;
        this._fluidSim!.maxAcceleration = 48000;

        // Create materials
        // this._boxMaterial = new BABYLON.PBRMaterial("BoxMeshMat", this._scene);
        // this._boxMaterial.metallic = 0.3;
        // this._boxMaterial.roughness = 0;
        // this._boxMaterial.alpha = 0.0;
        // this._boxMaterial.backFaceCulling = true;
        // this._boxMaterial.cullBackFaces = false;

        // this._boxMaterialFront = this._boxMaterial.clone("BoxMeshFrontMat");
        // this._boxMaterialFront.cullBackFaces = true;

        // Create meshes
        // this._boxMesh = BABYLON.MeshBuilder.CreateBox(
        //     "boxMesh",
        //     {
        //         width: this._boxMax.x - this._boxMin.x,
        //         height: this._boxMax.y - this._boxMin.y,
        //         depth: this._boxMax.z - this._boxMin.z,
        //     },
        //     this._scene
        // );
        // this._boxMesh.material = this._boxMaterial;
        // this._boxMesh.position.x = (this._boxMin.x + this._boxMax.x) / 2;
        // this._boxMesh.position.y = (this._boxMin.y + this._boxMax.y) / 2;
        // this._boxMesh.position.z = (this._boxMin.z + this._boxMax.z) / 2;
        // this._boxMesh.isPickable = false;

        // this._boxMeshFront = this._boxMesh.clone("boxMeshFront");
        // this._boxMeshFront.material = this._boxMaterialFront;
        // this._boxMeshFront.layerMask = 0x10000000; // make sure the mesh is not displayed by the camera - we will display it ourselves by a direct call to render()

        // Render the front side of the box
        // this._passPP.onApplyObservable.add((effect) => {
        //     effect.setTexture("textureSampler", camera!.outputRenderTarget);
        // });

        // let depthIsShared = false;
        // this._sceneAfterCameraRenderObserver =
        //     this._scene.onAfterCameraRenderObservable.add(() => {
        //         const firstPP = camera?._getFirstPostProcess();
        //         if (
        //             firstPP &&
        //             firstPP.inputTexture.depthStencilTexture &&
        //             !depthIsShared
        //         ) {
        //             firstPP.inputTexture._shareDepth(
        //                 camera!.outputRenderTarget!.renderTarget!
        //             );
        //             depthIsShared = true;
        //         }
        //         if (depthIsShared) {
        //             this._boxMeshFront?.render(
        //                 this._boxMeshFront.subMeshes[0],
        //                 true
        //             );
        //             this._scene.postProcessManager.directRender(
        //                 [this._passPP!],
        //                 null
        //             );
        //         }
        //     });

        this._onEngineResizeObserver = this._engine.onResizeObservable.add(
            () => {
                camera?.outputRenderTarget?.resize({
                    width: this._engine.getRenderWidth(true),
                    height: this._engine.getRenderHeight(true),
                });
                // depthIsShared = false;
            }
        );

        super._run();
        setTimeout(() => {
            document.getElementById("renderCanvas")!.style.opacity = "1.0";
        }, 800)
    }

    public dispose(): void {
        super.dispose();

        const camera =
            this._scene.activeCameras?.[0] ?? this._scene.activeCamera;

        if (camera) {
            camera.outputRenderTarget?.dispose();
            camera.outputRenderTarget = null;
        }

        this._scene.onBeforeRenderObservable.remove(this._sceneRenderObserver);
        this._scene.onAfterCameraRenderObservable.remove(
            this._sceneAfterCameraRenderObserver
        );
        this._scene.onKeyboardObservable.remove(this._sceneKeyboardObserver);

        // this._passPP.dispose();
        this._boxMesh?.dispose();
        this._boxMeshFront?.dispose();
        this._boxMaterial?.dispose();
        this._engine.onResizeObservable.remove(this._onEngineResizeObserver);
    }

    protected _makeGUIMainMenu(): void {
        const params = {
            checkXZBounds: true,
            autoRotateBox: false,
            restart: () => {
                this._angleX = this._angleY = 0;
                this._autoRotateBox = false;
                autoRotateBoxCtrl?.setValue(false);
                this._rotateMeshes(0, 0);
                this._generateParticles();
            },
            // boxOpacity: this._boxMaterial!.alpha,
        };

        const mainMenu = this._gui!;

        let autoRotateBoxCtrl: BABYLON.Nullable<LiLGUI.Controller> = null;

        mainMenu.add(params, "restart").name("Restart");

        mainMenu
            .add(params, "checkXZBounds")
            .name("Check box bounds")
            .onChange((value: boolean) => {
                this._checkXZBounds = value;
                this._boxMesh?.setEnabled(value);
                this._boxMeshFront?.setEnabled(value);
                for (let i = 0; i < this._collisionPlanes.length; ++i) {
                    this._collisionPlanes[i][1].disabled =
                        (!value && i < this._collisionPlanes.length - 1) ||
                        (value && i === this._collisionPlanes.length - 1);
                }
                if (!value) {
                    this._autoRotateBox = false;
                    autoRotateBoxCtrl?.setValue(false);
                }
            });

        autoRotateBoxCtrl = mainMenu
            .add(params, "autoRotateBox")
            .name("Auto rotate box")
            .onChange((value: boolean) => {
                this._autoRotateBox = value;
            });

        // mainMenu
        //     .add(params, "boxOpacity", 0, 1, 0.01)
        //     .name("Box opacity")
        //     .onChange((value: any) => {
        //         this._boxMaterial!.alpha = value;
        //         this._boxMaterialFront!.alpha = value;
        //     });
    }

    protected _onPaused(value: boolean) {
        super._onPaused(value);

        if (value) {
            this._autoRotateBox = false;
        }
    }

    protected _rotateMeshes(angleX: number, angleY: number): void {
        const transfo = BABYLON.Matrix.RotationYawPitchRoll(
            angleX,
            0,
            angleY,
        );

        const boxVertices = [
            new BABYLON.Vector3(this._boxMin.x, this._boxMin.y, this._boxMin.z),
            new BABYLON.Vector3(this._boxMin.x, this._boxMax.y, this._boxMin.z),
            new BABYLON.Vector3(this._boxMin.x, this._boxMax.y, this._boxMax.z),
            new BABYLON.Vector3(this._boxMin.x, this._boxMin.y, this._boxMax.z),
            new BABYLON.Vector3(this._boxMax.x, this._boxMin.y, this._boxMin.z),
            new BABYLON.Vector3(this._boxMax.x, this._boxMax.y, this._boxMin.z),
            new BABYLON.Vector3(this._boxMax.x, this._boxMax.y, this._boxMax.z),
            new BABYLON.Vector3(this._boxMax.x, this._boxMin.y, this._boxMax.z),
        ];

        let ymin = 1e10;
        for (let i = 0; i < boxVertices.length; ++i) {
            const v = BABYLON.Vector3.TransformCoordinates(
                boxVertices[i],
                transfo
            );
            ymin = Math.min(ymin, v.y);
        }

        this._collisionPlanes[
            this._origCollisionPlanes.length - 1
        ][1].params[1] = Math.abs(ymin) + 0.02;

        for (let i = 0; i < this._origCollisionPlanes.length - 1; ++i) {
            const plane = this._origCollisionPlanes[i].transform(transfo);
            this._collisionPlanes[i][1].params = [plane.normal, plane.d];
        }

        const quat = BABYLON.Quaternion.FromRotationMatrix(transfo);

        this._prevTransfo.invert();

        if (this._sphereMesh) {
            const tmp = BABYLON.Vector3.TransformCoordinates(
                this._sphereMesh.position,
                this._prevTransfo
            );

            this._sphereMesh.rotationQuaternion = quat;
            this._sphereMesh.position = BABYLON.Vector3.TransformCoordinates(
                tmp,
                transfo
            );
        }

        if (this._wallMesh) {
            const tmp = BABYLON.Vector3.TransformCoordinates(
                this._wallMesh.position,
                this._prevTransfo
            );

            const m = BABYLON.Matrix.RotationYawPitchRoll(
                0,
                (90 * Math.PI) / 180,
                0
            );
            const t = BABYLON.Matrix.Translation(tmp.x, tmp.y, tmp.z);

            m.multiplyToRef(t, m).multiplyToRef(transfo, m);
            const qf =
                this._wallMesh.rotationQuaternion ?? new BABYLON.Quaternion();

            m.decompose(undefined, qf, this._wallMesh.position);

            this._wallMesh.rotationQuaternion = qf;
        }

        if (this._boxMesh && this._boxMeshFront) {
            this._boxMesh.rotationQuaternion =
                this._boxMeshFront.rotationQuaternion = quat;
            this._boxMesh.position.x = (this._boxMin.x + this._boxMax.x) / 2;
            this._boxMesh.position.y = (this._boxMin.y + this._boxMax.y) / 2;
            this._boxMesh.position.z = (this._boxMin.z + this._boxMax.z) / 2;
            this._boxMesh.position = BABYLON.Vector3.TransformCoordinates(
                this._boxMesh.position,
                transfo
            );
            this._boxMeshFront.position = this._boxMesh.position;
        }

        this._prevTransfo.copyFrom(transfo);
    }
}
